}
.booking-form label {
  position: relative;
  width: 100%;
  display: block;
  font-family: "Montserrat", "Arial", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 10px;
  line-height: 13px;
  color: #f9fbfd;
  padding-left: 22px;
}
.booking-form label:hover {
  opacity: 0.7;
  cursor: pointer;
}
.booking-form label::before {
  content: "";
  width: 18px;
  height: 18px;
  position: absolute;
  top: -3px;
  left: -1px;
  background-color: #f9fbfd;
}
.booking-form input[name=user-approval]:checked + label::before {
  background-image: url("../img/svg/form-approval.svg");
  background-repeat: no-repeat;
  background-position: center;
}
.booking-form input[name=user-approval] {
  margin-left: 8px;
  margin-top: 10px;
}
.booking-form button {
  width: 100%;
  min-height: 43px;
  font-family: "Montserrat", "Arial", sans-serif;
  font-style: normal;
  font-weight: 600;
  font-size: 14px;
  line-height: 21px;
  color: #011c40;
  border: none;
  margin-top: 32px;
  background-color: #f9fbfd;
  cursor: pointer;
}
.booking-form button:hover, .booking-form button:focus {
  background-color: rgba(255, 255, 255, 0.8);
}

.booking-form__field {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  width: 100%;
  margin-top: 4px;
}
.booking-form__field:focus-within {
  outline: 1px solid #f9fbfd;
  outline-offset: 5px;
}

@media (max-width: 1023px) {
  .booking-form {
    max-width: 403px;
  }
  .booking-form label::before {
    left: 0;
  }
}
@media (max-width: 767px) {
  .booking-form {
    padding: 34px 30px 40px 30px;
  }
  .booking-form h3 {
    font-size: 18px;
    line-height: 25px;
    text-align: center;
    margin-bottom: 28px;
  }
  .booking-form label {
    padding-left: 28px;
  }
  .booking-form button {
    margin-top: 26px;
  }
  .booking-form label::before {
    top: 2px;
    left: 0;
    width: 20px;
    height: 20px;
  }
  .booking-form br {
    display: block;
  }
  .booking-form input[name=user-email] {
    margin-bottom: 15px;
  }
}
@media (min-width: 390px) and (max-width: 767px) {
  .booking-form label::before {
    top: -3px;
  }
}
