  height: 306px;
}
.contacts__map iframe {
  width: 100%;
  height: 306px;
  border: none;
}

.contacts__text-wrapper {
  max-width: 500px;
  align-self: center;
}

@media (min-width: 1024px) and (max-width: 1300px) {
  .contacts__text-wrapper {
    max-width: 370px;
  }
  .contacts__map {
    width: 400px;
  }
}
@media (max-width: 1023px) {
  .contacts {
    padding: 100px 0 97px 0;
  }
  .contacts p {
    margin-bottom: 27px;
  }
  .contacts .contacts__container {
    padding: 0 45px;
  }
  .contacts__text-wrapper {
    max-width: 325px;
    align-self: center;
  }
  .contacts__map {
    width: 324px;
    height: 338px;
