.map {
  width: 560px;
  height: 306px;
  overflow: hidden;
}

.contacts-list {
  margin: 0;
  padding: 0;
  list-style-type: none;
}

.contacts-list__link {
  position: relative;
  font-family: "Montserrat", "Arial", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  color: #011c40;
}
.contacts-list__link:hover {
  color: rgba(1, 28, 64, 0.7);
}

.contacts-list__link--adress br {
  display: none;
}

.contacts-list__item {
  position: relative;
  padding-left: 28px;
}

.contacts-list__item:not(:last-child) {
  margin-bottom: 22px;
}

.contacts-list__item::before {
  content: "";
  width: 18px;
  height: 22px;
  position: absolute;
  top: 1px;
  left: 0;
  background-repeat: no-repeat;
  background-position: center;
}

.contacts-list__item--phone::before {
  background-image: url("../img/svg/phone-icon.svg");
}

.contacts-list__item--adress::before {
  background-image: url("../img/svg/map-marker-icon.svg");
}

@media (min-width: 1024px) and (max-width: 1300px) {
  .contacts-list__item::before {
    top: 4px;
  }
}
@media (max-width: 1023px) {
  .contacts-list__item--phone::before {
    top: -1px;
  }
  .contacts-list__item--adress::before {
    top: 4px;
  }
  .contacts-list__link--adress br {
    display: block;
  }
}
