.features-list {
  margin: 0;
  padding: 0;
  list-style-type: none;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  column-gap: 40px;
  row-gap: 40px;
}

@media (min-width: 1024px) and (max-width: 1360px) {
  .features-list {
    grid-template-columns: repeat(2, 1fr);
  }

  .features-list__item {
    width: 400px;
  }
}

@media (max-width: 1023px) {
  .features-list {
    column-gap: 30px;
    row-gap: 30px;
  }
}

@media (max-width: 767px) {
  .features-list {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
}
